import { auth } from '../firebase/index';
import { formatChatDate } from './formatDate';
/**
 *  function responsbile for adding class which is reponsbile for the border radius of message cloud - for friend message
 * @param num - number needed to check if the message is first
 * @param last - boolean value needed to check uf the message is last
 */
var getFriendCloudClassName = function (num, last) {
    if (num === 0) {
        return 'message__cloud-friendFirst';
    }
    if (last) {
        return 'message__cloud-friendLast';
    }
};
/**
 * function responsbile for adding class which is reponsbile for the border radius of message cloud - for user message
 * @param num - number needed to check if the message is first
 * @param last - boolean value needed to check uf the message is last
 */
var getUserCloudClassName = function (num, last) {
    if (num === 0) {
        return 'message__cloud-userFirst';
    }
    if (last) {
        return 'message__cloud-userLast';
    }
};
/**
 * get html code for message
 * @param friend - data about friend, needed to create message with appropriate styles
 * @param currentUser - data about current user, needed to create message  with appropriate styles
 * @param message - data about message
 */
export var getMessageCode = function (friend, currentUser, message) {
    // check if message was sent by a friend in order to add appropriate styles
    if (message.userId !== auth.currentUser.uid) {
        return "\n    <div class='message__portraitWrapper'>\n       <div class='message__portrait'> \n         <img src='" + friend.portrait + "'/>\n       </div>\n    </div>\n\n    <div class='message__content message__content-friend'> \n      <strong class='message__nick'>" + friend.nick + "</strong>\n      " + message.content.map(function (el, num) {
            return "<div class='message__cloud message__cloud-friend " + getFriendCloudClassName(num, num === message.content.length - 1) + "'>\n          " + el + "\n          </div>";
        }).join('') + "\n        <strong>" + formatChatDate(message.createdAt) + "</strong>\n    </div>\n    ";
    }
    else {
        return "\n    <div class='message__content message__content-user'> \n      <strong class='message__nick'>You</strong>\n      " + message.content.map(function (el, num) {
            return "\n          <div class='message__cloud message__cloud-user " + getUserCloudClassName(num, num === message.content.length - 1) + "'>" + el + "</div>\n          \n          ";
        }).join('') + "\n        <strong>" + formatChatDate(message.createdAt) + "</strong>\n    </div>\n\n    <div class='message__portraitWrapper'>\n       <div class='message__portrait'> \n         <img src='" + currentUser.portrait + "'/>\n       </div>\n    </div>\n    ";
    }
};
