import { auth } from '../firebase/index';
/**
 * Class responsbile for general operations - logout and refreshing page on resize
 *  */
var General = /** @class */ (function () {
    function General() {
        this.logoutBtn = document.querySelector('#btn-logout');
        this.nav = document.querySelector('.nav');
        this.init();
    }
    ;
    // click event applied on button in order to logout the user when he presses a button
    General.prototype.logoutEvent = function () {
        this.logoutBtn.addEventListener("click", function () {
            return auth.signOut();
        });
    };
    ;
    // prevent of hiding navigation
    General.prototype.mobile = function () {
        var _this = this;
        var resize = function () {
            if (window.innerHeight <= 1000 && window.innerWidth >= 1024) {
                // check if navigation is hide
                if (_this.nav.style.display === 'none') {
                    _this.nav.style.display = 'block';
                }
            }
        };
        window.addEventListener('resize', resize);
    };
    // iniclalization of scripts
    General.prototype.init = function () {
        this.logoutEvent();
        this.mobile();
    };
    ;
    return General;
}());
export { General };
;
