/**
 *  get available blacksmith slots picks, each slot has only 2 picks (user can only buy 2 items from one slot)
 */
export var getBlacksmithPicks = function () {
    return [
        {
            index: 0,
            picks: 2
        },
        {
            index: 1,
            picks: 2
        },
        {
            index: 2,
            picks: 2
        },
        {
            index: 3,
            picks: 2
        },
        {
            index: 4,
            picks: 2
        },
        {
            index: 5,
            picks: 2
        },
    ];
};
