/**
 * class responsible for mobile nav which is displaying below 1024px
 */
var MobileNav = /** @class */ (function () {
    function MobileNav() {
        this.dom = {
            navContainer: document.querySelector('.nav'),
            navIcon: document.querySelector('.mobileNav__icon'),
            links: document.querySelectorAll('.nav a'),
        };
        this.init();
    }
    // click event applied on button by which user can toggle container with navigation
    MobileNav.prototype.toggleNav = function () {
        var _this = this;
        this.dom.navIcon.addEventListener('click', function () {
            // check if nav container is active
            var flag = _this.dom.navContainer.style.display === 'block';
            if (flag) {
                _this.dom.navContainer.style.display = 'none';
                _this.dom.navIcon.src = './images/menu.png';
            }
            else {
                _this.dom.navContainer.style.display = 'block';
                _this.dom.navIcon.src = './images/close.png';
            }
        });
    };
    // click events applied on each link in navigation, when user click one of these links then hide container with navigation
    MobileNav.prototype.hideNav = function () {
        var _this = this;
        this.dom.links.forEach(function (el) { return el.addEventListener('click', function () {
            if (_this.dom.navContainer.style.display === 'block') {
                _this.dom.navContainer.style.display = 'none';
            }
            _this.dom.navIcon.src = './images/menu.png';
        }); });
    };
    // initialization of scripts
    MobileNav.prototype.init = function () {
        this.toggleNav();
        this.hideNav();
    };
    return MobileNav;
}());
export { MobileNav };
